.container {
    --_blue-box-shodow-color: rgba(0, 77, 170, 0.79);
    --_gray-box-shodow-color: rgba(63, 64, 64, 0.79);
    --_container-box-shadow-color: var(--_gray-box-shodow-color);

    padding: 5px;
    background-color: var(--com-bg-clr);
    border-radius: 8px;
    max-height: 90%;
    background: radial-gradient(circle at 100% 100%, #121212 0, #121212 4px, transparent 4px) 0% 0%/8px 8px no-repeat,
                radial-gradient(circle at 0 100%, #121212 0, #121212 4px, transparent 4px) 100% 0%/8px 8px no-repeat,
                radial-gradient(circle at 100% 0, #121212 0, #121212 4px, transparent 4px) 0% 100%/8px 8px no-repeat,
                radial-gradient(circle at 0 0, #121212 0, #121212 4px, transparent 4px) 100% 100%/8px 8px no-repeat,
                linear-gradient(#121212, #121212) 50% 50%/calc(100% - 8px) calc(100% - 16px) no-repeat,
                linear-gradient(#121212, #121212) 50% 50%/calc(100% - 16px) calc(100% - 8px) no-repeat,
                repeating-linear-gradient(355deg, #a91cad 0%, #004daa 14%, rgba(0, 77, 170, 1) 20%, rgba(0, 77, 170, 1) 20%, #004daa 30%, #004daa 50%, #009fff 57%, #009fff 80%, #004daa 90%);


    box-shadow: 1px 11px 34px 6px var(--_container-box-shadow-color);
    -webkit-box-shadow: 1px 11px 34px 6px var(--_container-box-shadow-color);
    -moz-box-shadow: 1px 11px 34px 6px var(--_container-box-shadow-color);
    opacity: .95;
    z-index: 1;
    display: flex;
}

.container div.container-div{
    padding: 25px;
    overflow: auto; 
}

.container div.container-div::-webkit-scrollbar-track,
.container::-webkit-scrollbar-track {
    border: 1px solid #000;
    padding: 2px 0;
    background-color: #fff;
    border-radius: 17px;
}

.container div.container-div::-webkit-scrollbar,
.container::-webkit-scrollbar {
    width: 8px;
}

.container div.container-div::-webkit-scrollbar-thumb,
.container::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #737272;
    border: 1px solid #000; 
}

@media (min-width: 560px) {
    .container {
        max-width: 85%;
        max-height: 90%;
    }
}

@media (min-width: 900px) {
    .container {
        max-width: 50%;
        max-height: 90%;
    }
}