.login-box-conatiner{
    /* background-image: linear-gradient(
        to right bottom,
        rgba(33, 33, 33, 0.8),
        rgba(0, 0, 0, 0.8) 
        ) , url('https://img.freepik.com/free-vector/network-mesh-wire-digital-technology-background_1017-27428.jpg?w=1380&t=st=1696380991~exp=1696381591~hmac=2d730958d4861577f90c8bc91bf807c269b59028a0c2cfbe31cc0c69caecaefd'); */
    background-image: linear-gradient(
        to right bottom,
        rgba(33, 33, 33, 0.5),
        rgba(0, 0, 0, 0.5) 
        ),url('../../Assets/Img/Concorde\ elements-03.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 120vw;

    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    height: 100vh;
    z-index: 2;
    position: relative;
    overflow: hidden;
}

.header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.header h2{
    letter-spacing: 1.2px;
    text-align: center;
}

.logo-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo-container img{
    width: 60%;
    padding-bottom: 15px;
    border-bottom: 1.5px solid var(--primary-color);
}

.login-options-group{
    text-align: center;
    padding: 10px 55px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    backdrop-filter: blur(60px);
    cursor: pointer;
}

.login-options-group p{
    letter-spacing: 1px;
}

.login-options-group:hover{
    box-shadow: 0px 0px 11px 4px rgba(255, 255, 255, 0.2);
}

.login-options-group:hover p{
    letter-spacing: 1.3px;
}

.login-options-group:hover svg{
    scale: 1.05;
}

.login-options-group:active{
    box-shadow: 0px 0px 11px 4px var(--primary-color);
}